import { MainPage, PodcastPage, PodcastListPage, ArticlePage, ArticleListPage, AboutPage } from '../pages';

interface RouteItem {
  path: string
  component: any
  exact: boolean
}

const routes: Array<RouteItem> = [
  {
    // 主页
    path: '/',
    component: MainPage,
    exact: true,
  },
  {
    // 文章列表页
    path: '/article',
    component: ArticleListPage,
    exact: true,
  },
  {
    // 播客列表页
    path: '/podcast',
    component: PodcastListPage,
    exact: true,
  },
  {
    // 文章页
    path: '/article/:id',
    component: ArticlePage,
    exact: false,
  },
  {
    // 播客页
    path: '/podcast/:id',
    component: PodcastPage,
    exact: false,
  },
  {
    // 关于页
    path: '/about',
    component: AboutPage,
    exact: false,
  },
];

export default routes;
