import React from 'react';
import { Link } from 'react-router-dom';
import style from './show-more.scss';

export default class ShowMore extends React.Component<any, any> {
  constructor(props: any) {
    super(props);
  }
  render() {
    const { href } = this.props;
    return <Link to={href} className={style.show_more}>
      查看更多
    </Link>
  }
}