import React, { Component } from 'react';
import ReactPlayer from 'react-player';
import { timeConvert } from '../../modules/utils';
import style from './play-bar.scss';

class PlayBar extends Component<any, any> {
  constructor(props:any) {
    super(props);
  }

  state = {
    playing: false, // 播放状态
    url: null,
    currentTime: 0, // 已播放时间
    backgroundsize: 0,
  }

  onSeekChange = e => {
    if (this.state.playing || this.state.currentTime !== 0) {
      const voice_timelen = 5123;
      this.setState({
        currentTime: `${parseFloat(e.target.value) * voice_timelen / 100}`,
        backgroundsize: `${parseFloat(e.target.value)}%`,
      });
      this.player.seekTo(`${parseFloat(e.target.value) * voice_timelen / 100}`);
    }
  }

  playPause() {
    this.setState({ playing: !this.state.playing });
  }

  render() {
    // const { title, intro, time } = this.props;
    const { playing, currentTime, backgroundsize } = this.state;
    const voice_title = '#52 我们的得意忘形';
    const voice_timelen = 5123;
    const voice_url = 'http://audio.xmcdn.com/group56/M0A/A5/7A/wKgLgFxX9KKhYmqGAnjJjJjUKkI950.m4a';
    return (
      <div className={style.play_bar}>
        <div className={style.btm_bar}>
          <div className={style.player_wrapper}>
            <div
              onClick={() => this.playPause()}
              className={style.suspend}
              style={{ backgroundPosition: `${playing ? '-30px 0' : '0 0'}` }}
            />
            <div className={style.content}>
              <div className={style.title}>{voice_title}</div>
              <div className={style.duration}>
                <input
                  type="range"
                  className={style.audioLine}
                  style={{ backgroundSize: `${backgroundsize} 100%` }}
                  value={`${currentTime / voice_timelen * 100}`}
                  onChange={this.onSeekChange}
                />
              </div>
            </div>
            
            <div className={style.time}>
              <span>{timeConvert(currentTime)}</span>
              <span className={style.slash}>/</span>
              <span className={style.total}>
                {timeConvert(voice_timelen - 1)}
              </span>
            </div>
            <div className={style.past}>
              <span>三</span>
              <span>52</span>
            </div>
            
            <ReactPlayer
              url={voice_url}
              width="0"
              height="0"
              ref={ref => { this.player = ref; }}
              playing={playing}
              onPlay={() => this.setState({ playing: true })}
              onPause={() => this.setState({ playing: false })}
              onEnded={() => this.setState({
                currentTime: 0,
                playing: false,
                backgroundsize: 0,
              })}
              onError={e => console.log('onError', e)}
              onProgress={e => {
                if (this.state.playing) {
                  this.setState({
                    currentTime: e.playedSeconds,
                    backgroundsize: `${e.played * 100}%`,
                  });
                }
              }}
            />
          </div>
        </div>
      </div>
    );
  }
}

export default PlayBar;
