// 秒数转换
export const timeConvert = timestamp => {
  let minutes = Math.floor(timestamp / 60);
  let seconds = Math.floor(timestamp - (minutes * 60));

  if (seconds < 10) {
    seconds = `0${seconds}`;
  }
  if (minutes < 10) {
    minutes = `0${minutes}`;
  }

  const timestamps = `${minutes}:${seconds}`;
  return timestamps;
};