// 文章列表页
import React, { Component } from 'react'
import chunk from 'lodash.chunk'
import ReactPaginate from 'react-paginate'
import cx from 'classnames'
import { ArticleCell } from '../../components'
import style from './article-list.scss'

class ArticleListPage extends Component<any, any> {
  constructor(props: any) {
    super(props)
  }
  state = {
    sequence: true, // 列表顺序true：正序，false：倒序
    tag: 0,
    data: [],
    offset: 0,
    pageCount: 16,
  }
  loadArticle() {

  }
  handlePageClick = data => {
    let selected = data.selected;
    let offset = Math.ceil(selected * this.props.perPage);

    this.setState({ offset: offset }, () => {
      this.loadArticle();
    });
  };
  
  render() {
    const ArticleList = chunk([...Array(9).keys()], 3)
    const tags = ['全部（51）', '财商（20）', '思维方式（118）', '网球（6）', '哲学（9）']
    const { sequence, tag } = this.state
    return (<div className={style.ArticleListPage}>
      <div className={style.article_list}>
        <div className={style.bar}>
          <span className={style.title}>全部播客</span>
          <i className={cx(style.sequence, sequence && style.selected)}>&#xe651;</i>
          <i className={cx(style.sequence, !sequence && style.selected)}>&#xe651;</i>
        </div>
        {ArticleList.map((threeArticle, index) => (
          <div className={style.article_cover} key={`ArticleCover${index}`}>
            {threeArticle.map((item, index2) => (<ArticleCell
              key={`ArticleCell${index2}`}
              id={index2}
              title={'关于求职类问题的一种解答和一个思考框架'}
              img={'http://mat1.gtimg.com/www/js/news/test_img.png'}
              time={'2017 年 8 月 9 日'}
              intro={'不知道是不是每年开学季+秋招季又到了，最知乎微博私信都涌进来各种关于求职的问题。在此统一回答一下，也算说说自己的看法：首先，所有建议里面我最想讲的第一条就是，期待别人能理解你的境况并且给你一个标准答案是不现实的。职业选择这种大事…涉到生活的方方面面，每个人决策的优先级都不同。所以不管你问谁，得到什么样的回复，最好都别太当回事。'}
            />))}
          </div>
        ))}
        <ReactPaginate
          previousLabel={'<'}
          nextLabel={'>'}
          breakLabel={'...'}
          pageCount={this.state.pageCount}
          marginPagesDisplayed={1}
          pageRangeDisplayed={3}
          onPageChange={this.handlePageClick}
          containerClassName={style.pagination}
          activeClassName={style.active}
          breakClassName={style.break_me}
        />
      </div>
      <div className={style.tag_box}>
        {tags.map((item, index) => (
          <div className={cx(style.tag_item, tag === index && style.tag_selected)}>{item}</div>
        ))}
      </div>
    </div>)
    }
  }
    
export default ArticleListPage