import React, { PureComponent } from 'react';
import BraftEditor from 'braft-editor';
import cx from 'classnames';
import { Link } from 'react-router-dom';
import style from './podcast-cell.scss';

class PodcastCell extends PureComponent<any, any> {
  constructor(props:any) {
    super(props);
  }
  static defaultProps = {
    onClick: () => {},
  };

  render() {
    const { id, title, intro, time, timelength, theme, onClick } = this.props;
    const editorState = BraftEditor.createEditorState(intro)

    return (
      <Link to={`/podcast/${id}`}>
        <div className={style.podcast_cell}>
          <a href="" title={title} onClick={onClick}>
            <i className={style.play_icon}>&#xe652;</i>
          </a>
          <div>
            <div className={style.title}>{title}</div>
            <div className={style.time}>
              <i className={style.logo}>&#xe659;</i>
              <div>{time}</div>
              <i className={style.logo}>&#xe654;</i>
              <div>{timelength}</div>
            </div>
            <div className={cx(style.intro, theme === 'mini' && style.theme_mini)} dangerouslySetInnerHTML={{__html: `${editorState.toHTML()}`}} />
          </div>
        </div>
      </Link>
    );
  }
}

export default PodcastCell;
