import React from 'react';
import { Link } from 'react-router-dom';
import style from './header.scss';

interface PageItem {
  textContent: string
  href: string
}

const pageList: Array<PageItem> = [
  {
    textContent: '播客',
    href: '/podcast'
  },
  {
    textContent: '文章',
    href: '/article'
  },
  {
    textContent: '关于',
    href: '/about'
  }
]

export default class Header extends React.Component<any, any> {
  constructor(props: any) {
    super(props);
  }
  render() {
    return <header>
      <div className={style.headerCover}>
        <Link to='/' className={style.logoCover}>
          <i className={style.logo}>&#xe657;</i>
          <div className={style.logoName}>得意忘形</div>
        </Link>
        <div className={style.routerCover}>
          {pageList.map((item:PageItem, index:number) => {
            return (
              <Link to={item.href} key={index} className={style.router}>{item.textContent}</Link>
            )
          })}
        </div>
      </div>
    </header>
  }
}