import React, { PureComponent } from 'react';
import { Link } from 'react-router-dom';
import style from './article-cell.scss';

class ArticleCell extends PureComponent<any, any> {
  constructor(props:any) {
    super(props);
  }

  render() {
    const { id, title, intro, img, time } = this.props;
    return (
      <Link to={`/article/${id}`} className={style.article_cell}>
        <img className={style.img} src={img} />
        <div className={style.content}>
          <div className={style.title}>{title}</div>
          <div className={style.time}>{time}</div>
          <div className={style.intro}>{intro}</div>
        </div>
      </Link>
    );
  }
}

export default ArticleCell;
